.container {

  &__menu {
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    align-items: flex-start;
    gap: 30px;

    @include onTablet {
      display: flex;
      flex-direction: row;
    }

    @include onDesktop {
      display: flex;
      flex-direction: row;
    }
  }

  &__header-menu--form {
    display: none;
    margin-top: 0;

    @include onTablet {
      display: inline-block;
    }

    @include onDesktop {
      display: inline-block;
    }
  }

}
